/* ----------------------------- Kyiv Type Sans ----------------------------- */

@font-face {
    font-family: $kyivTypeSans;
    font-style: normal;
    font-weight: 400;
    src: url("../assets/fonts/kyivTypeSans/KyivTypeSans-Regular-.eot"); /* IE9 Compat Modes */
    src: local(""),
      /* IE6-IE8 */
        url("../assets/fonts/kyivTypeSans/KyivTypeSans-Regular-.woff2")
        format("woff2"),
      /* Super Modern Browsers */
        url("../assets/fonts/kyivTypeSans/KyivTypeSans-Regular-.woff")
        format("woff"),
      /* Modern Browsers */
        url("../assets/fonts/kyivTypeSans/KyivTypeSans-Regular-.ttf")
        format("truetype"),
  }

  @font-face {
    font-family: $kyivTypeSans;
    font-style: normal;
    font-weight: 500;
    src: url("../assets/fonts/kyivTypeSans/KyivTypeSans-Medium-.eot"); /* IE9 Compat Modes */
    src: local(""),
      /* IE6-IE8 */
        url("../assets/fonts/kyivTypeSans/KyivTypeSans-Medium-.woff2")
        format("woff2"),
      /* Super Modern Browsers */
        url("../assets/fonts/kyivTypeSans/KyivTypeSans-Medium-.woff")
        format("woff"),
      /* Modern Browsers */
        url("../assets/fonts/kyivTypeSans/KyivTypeSans-Medium-.ttf")
        format("truetype"),
  }


